*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
 
}
span{
    display: flex;
    font-size: 4vh;
    margin-bottom: 0;
}
.mainc{
    width: 100%;
    height: 100vh;
display: flex;
flex-direction: row;
align-items: center;
justify-content: center;
text-align: center;
background: linear-gradient(rgb(72, 247, 244),rgb(177, 252, 124),white);
}
.center_div{
    width: 30%;
    height: 90vh;
    background-color: rgb(86, 85, 125);
    box-shadow: 15px 15px 15px 0px rgb(63, 53, 53);
    border-radius: 12px 0px 0px 12px;

}
h1{
    color: rgb(0, 0, 0);
    background: transparent ;
    background-color: rgb(202, 62, 69);
    padding: 6px 0px 2px 0px;
    margin-bottom: 10px;
    box-shadow: 5px 5px 5px 5px rgb(54, 48, 48); 
    height: 8vh;
    font-size: 7vh;
    
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
}
.pencil{    color: rgb(0, 0, 0);
    /* background: transparent ; */
    /* background-color: rgb(202, 62, 69); */
    padding: 6px 0px 2px 0px;
    margin-bottom: 10px;
    /* box-shadow: 5px 5px 5px 5px rgb(54, 48, 48);  */
    height: 5vh;
    font-size: 6vh;
    
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;

}
input{
    text-align: center;
    height:30px;
    top: 15px;
    border: none;
    background: transparent;
    font-size: 30px;
    font-weight: 500;
    width: 60%;
    border-bottom: 4px solid rgb(202, 62, 69);
    outline: none;
    font-family: Josefin Sans;
}
button{
    min-width: 40px;
    width: 45px;
    border-radius: 50%;
    border-color: transparent;
    background-color: slateblue;
    color: aliceblue;
    font-size: 40px;
    border: none;
    outline: none;
    margin-left: 10px;
    margin-top: 10px;
    box-shadow: 5px 5px 15px -5px black;
}
button:hover{
    background-color: rgb(207, 194, 19);
    color: black;
}
ol{
    margin-top: 30px;
}
ol li{
    padding-left: 0px;
    text-align: center;
    font-size: 40px;
    font-weight: 500;
    min-height: 40px;
    display: flex;
    align-items: center;
    color: black;
    text-transform: capitalize;
    font-family: Josefin Sans;
    padding-top: 10px;
    
}
.tudu_style{
    display: flex;
    flex-direction: row;
    justify-content: left;
    align-items: center;
}
.tudu_style .fa-times{
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: slateblue;
    border-radius: 50%;
    margin: 10px  15px 0 35px ;
    color: black;
    font-size: 30px;

    box-shadow: 5px 5px 15px -5px black;
}
.tudu_style .fa-times:hover{
    background-color: rgb(61, 230, 115);

}
.tudu_style .fa-times :hover .list_style{
    color: rgb(238, 73, 73);

}

@media (min-width:168px) and (max-width:768px) {
    .center_div{
        width: 75%;
    }
    .tudu_style .fa-times{
        margin: 0 15px 0 15px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    button{
        margin-left: 5px;
        min-height: 40px;
        width: 40px;
        color: aliceblue;
        font-size: 40px;
    }
    input{
        font-size: 2.5vh;
    }
    ol li{
        font-size: 3.6vh;
    }
    h1{
        font-size: 3vh;
    }
    
}
@media (min-width:768px ) and (max-width:1626px){

 h1{
        font-size: 5vh;
    }
    .center_div{
        width: 45%;
    }
    .tudu_style .fa-times{
        margin: 0 15px 0 15px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    /* button{
        margin-left: 5px;
        min-height: 40px;
        width: 5vh;
        height: 5vh;
        color: aliceblue;
        font-size: 50px;
    } */
    input{
        margin-top: 5vh;
        font-size: 4vh;
        height: 5vh;
    }
    ol li{
        font-size: 2.9vh;
        margin-bottom: 10px;
    }
}
@media (min-width:1626px ) and (max-width:2150px){

 h1{
        font-size: 7vh;
    }
    .center_div{
        width: 35%;
    }
    .tudu_style .fa-times{
        margin: 0 15px 0 15px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    button{
        margin-left: 5px;
        min-height: 40px;
        width: 5vh;
        height: 5vh;
        color: aliceblue;
        font-size: 40px;
    }
    input{
        margin-top: 5vh;
        font-size: 5vh;
        height: 5vh;
    }
    ol li{
        font-size: 3.6vh;
        margin-bottom: 10px;
    }
}